import { NgModule } from '@angular/core';
import {HttpModule} from '@angular/http';
import {ReactiveFormsModule} from '@angular/forms';
import { CommonModule } from '@angular/common';
import { ProductshowcaseComponent } from './productshowcase/productshowcase.component';
import {InsertproductComponent} from './insertproduct/insertproduct.component';
import {ProductServe} from './product.serve';
@NgModule({
  imports: [
    CommonModule,
    HttpModule,
    ReactiveFormsModule
  ],
  declarations: [ProductshowcaseComponent,
    InsertproductComponent],
  exports:[
    ProductshowcaseComponent
  ],
  providers:[ProductServe]
})
export class ProductModule { }



