import { Injectable } from '@angular/core';
import {Http,Headers,RequestOptions,Response} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Product} from './product';
@Injectable()
export class ProductServe {
  constructor(private http:Http){}

  productlisturl='http://localhost:4400/apitest/';
  productaddurl='http://localhost:4400/apitest/';
  getProductList():Observable<any[]>{
    return this.http.get(this.productlisturl)
    .map((response:Response)=>response.json());
  }
  addProductDetail(product:Product):Observable<any>{
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.productaddurl,product,options)
      .map((response:Response)=>console.log(response));
  }
}



