import { Component, OnInit } from '@angular/core';
import {ProductServe} from '../product.serve';

import {Product} from '../product';
@Component({
  selector: 'productshowcase',
  templateUrl: 'productshowcase.component.html',
})
export class ProductshowcaseComponent implements OnInit {
  productlist:Product[];
  constructor(private productserve:ProductServe) {}
  ngOnInit() {

    this.productserve.getProductList()
    .subscribe((dt)=>this.productlist=dt);
  }
}



