import { Component,OnInit } from '@angular/core';
import {ProductServe} from '../product.serve';
import{FormGroup,FormBuilder,FormArray,FormControl}from'@angular/forms'
@Component({
    moduleId: module.id,
    selector: 'insertproduct',
    templateUrl: './insertproduct.component.html',
    styleUrls: ['./insertproduct.component.css']
})
export class InsertproductComponent implements OnInit{
  rootFG:FormGroup;
  mesg:string='';

  constructor(private fb:FormBuilder,private productserve:ProductServe)
  {}
  ngOnInit(){
    this.rootFG=this.fb.group({
        'productId':[''],
        'productName':[null],
        'productDesc':[''],
        'productPrice':[0]
      });
  }
  addProduct(dt){
    this.productserve.addProductDetail(dt)
    .subscribe((data)=>this.mesg=data);
  }
}



